import React from "react";
import { Link, useLocation } from "react-router-dom";

const Sidebar = () => {
  const location = useLocation();

  const menu = [
    { name: "Dashboard", path: "/" },
    { name: "Employees", path: "/Employees" },
    { name: "Attendance Table", path: "/Attendance" },
    { name: "Upload CSV", path: "/attendance/upload" },
    { name: "Monthly Report", path: "/attendance/report" },
  ];

  return (
    <aside className="bg-gray-100 w-64 p-4 min-h-screen border-r">
      <ul className="space-y-2">
        {menu.map((item) => (
          <li key={item.name}>
            <Link
              to={item.path}
              className={`block p-2 rounded hover:bg-gray-200 ${
                location.pathname === item.path ? "bg-gray-300 font-semibold" : ""
              }`}
            >
              {item.name}
            </Link>
          </li>
        ))}
      </ul>
    </aside>
  );
};

export default Sidebar;
