import React, { useState, useEffect } from "react";
import EmployeeList from "./Employees/EmployeeList";
import EmployeeForm from "./Employees/EmployeeForm";
import EmployeeDetails from "./Employees/EmployeeDetails";

import AttendanceTable from "./Attendance/AttendanceTable";
import UploadAttendanceCSV from "./Attendance/UploadAttendanceCSV";
import MonthlyReport from "./Attendance/MonthlyReport";

const EmployeeAttendanceManager = () => {
  const [activeTab, setActiveTab] = useState("employees");
  const [showForm, setShowForm] = useState(false);
  const [selectedEmployee, setSelectedEmployee] = useState(null);
  const [viewEmployee, setViewEmployee] = useState(null);

  // Handles tab switch and closes modals automatically
  const switchTab = (tab) => {
    setActiveTab(tab);
    setShowForm(false);
    setViewEmployee(null);
  };

  const openAddForm = () => {
    setSelectedEmployee(null);
    setShowForm(true);
  };

  const openEditForm = (employee) => {
    setSelectedEmployee(employee);
    setShowForm(true);
  };

  const closeForm = () => {
    setShowForm(false);
    setSelectedEmployee(null);
  };

  const openDetails = (employeeId) => {
    setViewEmployee(employeeId);
  };

  const closeDetails = () => {
    setViewEmployee(null);
  };

  // Allow closing modal with Esc key
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === "Escape") {
        closeForm();
        closeDetails();
      }
    };
    window.addEventListener("keydown", handleKeyDown);
    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  return (
    <div className="p-4 md:p-6">
      {/* === Tabs === */}
      <div className="flex flex-wrap gap-4 mb-6 border-b border-gray-300">
        <button
          onClick={() => switchTab("employees")}
          className={`pb-2 text-lg font-medium transition-colors ${
            activeTab === "employees"
              ? "border-b-2 border-blue-600 text-blue-600"
              : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Employees
        </button>
        <button
          onClick={() => switchTab("attendance")}
          className={`pb-2 text-lg font-medium transition-colors ${
            activeTab === "attendance"
              ? "border-b-2 border-blue-600 text-blue-600"
              : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Attendance
        </button>
      </div>

      {/* === EMPLOYEE TAB === */}
      {activeTab === "employees" && (
        <>
          <div className="flex justify-between items-center mb-6">
            <h2 className="text-2xl font-semibold">Employee List</h2>
            <button
              onClick={openAddForm}
              className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition"
            >
              + Add Employee
            </button>
          </div>

          <EmployeeList onEdit={openEditForm} onView={openDetails} />

          {/* === Add/Edit Employee Modal === */}
          {showForm && (
            <div
              className="fixed inset-0 bg-black bg-opacity-40 flex justify-center items-center z-50"
              aria-modal="true"
              role="dialog"
            >
              <div className="bg-white p-6 rounded-xl shadow-lg max-w-full sm:w-[600px] relative">
                <button
                  onClick={closeForm}
                  aria-label="Close form"
                  className="absolute top-3 right-3 text-gray-500 hover:text-black text-lg"
                >
                  ✕
                </button>
                <EmployeeForm
                  selectedEmployee={selectedEmployee}
                  onClose={closeForm}
                />
              </div>
            </div>
          )}

          {/* === Employee Details Modal === */}
          {viewEmployee && (
            <EmployeeDetails id={viewEmployee} onClose={closeDetails} />
          )}
        </>
      )}

      {/* === ATTENDANCE TAB === */}
      {activeTab === "attendance" && (
        <div className="space-y-6">
          <AttendanceTable />
          <UploadAttendanceCSV />
          <MonthlyReport />
        </div>
      )}
    </div>
  );
};

export default EmployeeAttendanceManager;
