import React, { useContext, useEffect, useState } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { AttendanceContext } from '../../context/AttendanceContext';

const AttendanceTable = () => {
  const { attendance, loading, getAttendance, addAttendance, editAttendance, removeAttendance } = useContext(AttendanceContext);
  const [isEditing, setIsEditing] = useState(false);
  const [editId, setEditId] = useState(null);

  useEffect(() => {
    getAttendance();
  }, []);

  const formik = useFormik({
    initialValues: {
      employee_id: '',
      date: '',
      status: 'Present',
      in_time: '',
      out_time: '',
    },
    validationSchema: Yup.object({
      employee_id: Yup.string().required('Employee ID is required'),
      date: Yup.date().required('Date is required'),
      status: Yup.string().required('Status is required'),
    }),
    onSubmit: async (values, { resetForm }) => {
      try {
        if (isEditing) {
          await editAttendance(editId, values);
          setIsEditing(false);
          setEditId(null);
        } else {
          await addAttendance(values);
        }
        resetForm();
      } catch (err) {
        console.error('Error saving attendance:', err);
      }
    },
  });

  const handleEdit = (record) => {
    formik.setValues({
      employee_id: record.employee_id?._id || '',
      date: record.date || '',
      status: record.status || 'Present',
      in_time: record.in_time || '',
      out_time: record.out_time || '',
    });
    setEditId(record._id);
    setIsEditing(true);
  };

  const handleDelete = async (id) => {
    if (window.confirm('Are you sure you want to delete this record?')) {
      await removeAttendance(id);
    }
  };

  return (
    <div className="p-6 bg-white shadow-md rounded-lg">
      <h2 className="text-2xl font-semibold mb-4">Attendance Table</h2>

      {/* Attendance Form */}
      <form onSubmit={formik.handleSubmit} className="grid grid-cols-5 gap-3 items-end mb-6">
        <div className="flex flex-col">
          <input
            name="employee_id"
            value={formik.values.employee_id}
            onChange={formik.handleChange}
            placeholder="Employee ID"
            className="border p-2 rounded"
          />
          {formik.touched.employee_id && formik.errors.employee_id && (
            <p className="text-red-500 text-sm">{formik.errors.employee_id}</p>
          )}
        </div>

        <div className="flex flex-col">
          <input type="date" name="date" value={formik.values.date} onChange={formik.handleChange} className="border p-2 rounded" />
          {formik.touched.date && formik.errors.date && (
            <p className="text-red-500 text-sm">{formik.errors.date}</p>
          )}
        </div>

        <div className="flex flex-col">
          <select name="status" value={formik.values.status} onChange={formik.handleChange} className="border p-2 rounded">
            <option value="Present">Present</option>
            <option value="Absent">Absent</option>
            <option value="Leave">Leave</option>
          </select>
          {formik.touched.status && formik.errors.status && (
            <p className="text-red-500 text-sm">{formik.errors.status}</p>
          )}
        </div>

        <input type="time" name="in_time" value={formik.values.in_time} onChange={formik.handleChange} className="border p-2 rounded" />
        <input type="time" name="out_time" value={formik.values.out_time} onChange={formik.handleChange} className="border p-2 rounded" />

        <button type="submit" className="col-span-5 bg-blue-600 text-white py-2 rounded hover:bg-blue-700">
          {isEditing ? 'Update Attendance' : 'Add Attendance'}
        </button>
      </form>

      {/* Attendance List */}
      {loading ? (
        <p>Loading attendance...</p>
      ) : (
        <table className="w-full border-collapse">
          <thead>
            <tr className="bg-gray-100">
              <th className="border p-2">Employee</th>
              <th className="border p-2">Date</th>
              <th className="border p-2">Status</th>
              <th className="border p-2">In Time</th>
              <th className="border p-2">Out Time</th>
              <th className="border p-2">Actions</th>
            </tr>
          </thead>
          <tbody>
            {attendance.length === 0 ? (
              <tr>
                <td colSpan="6" className="text-center p-4 text-gray-500">No records found</td>
              </tr>
            ) : (
              attendance.map((record) => (
                <tr key={record._id}>
                  <td className="border p-2">{record.employee_id?.name || record.employee_id?._id || '-'}</td>
                  <td className="border p-2">{record.date ? new Date(record.date).toLocaleDateString() : '-'}</td>
                  <td className={`border p-2 font-semibold ${record.status === 'Present' ? 'text-green-600' : record.status === 'Absent' ? 'text-red-600' : 'text-yellow-600'}`}>
                    {record.status || '-'}
                  </td>
                  <td className="border p-2">{record.in_time || '-'}</td>
                  <td className="border p-2">{record.out_time || '-'}</td>
                  <td className="border p-2 flex gap-2 justify-center">
                    <button onClick={() => handleEdit(record)} className="text-blue-600 hover:underline">Edit</button>
                    <button onClick={() => handleDelete(record._id)} className="text-red-600 hover:underline">Delete</button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default AttendanceTable;
