import React, { useState } from "react";
import EmployeeList from "./Employees/EmployeeList";
import AttendanceTable from "./Attendance/AttendanceTable";
import UploadAttendanceCSV from "./Attendance/UploadAttendanceCSV";
import MonthlyReport from "./Attendance/MonthlyReport";

const Dashboard = () => {
  const [activeTab, setActiveTab] = useState("dashboard");

  const switchTab = (tab) => {
    setActiveTab(tab);
  };

  return (
    <div className="p-6">
      {/* Sidebar / Tabs */}
      <div className="flex gap-4 mb-6 border-b border-gray-300">
        <button
          onClick={() => switchTab("dashboard")}
          className={`pb-2 text-lg font-medium ${
            activeTab === "dashboard" ? "border-b-2 border-blue-600 text-blue-600" : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Dashboard
        </button>
        <button
          onClick={() => switchTab("employees")}
          className={`pb-2 text-lg font-medium ${
            activeTab === "employees" ? "border-b-2 border-blue-600 text-blue-600" : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Employee Management
        </button>
        <button
          onClick={() => switchTab("attendance")}
          className={`pb-2 text-lg font-medium ${
            activeTab === "attendance" ? "border-b-2 border-blue-600 text-blue-600" : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Attendance Table
        </button>
        <button
          onClick={() => switchTab("monthly")}
          className={`pb-2 text-lg font-medium ${
            activeTab === "monthly" ? "border-b-2 border-blue-600 text-blue-600" : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Monthly Report
        </button>
        <button
          onClick={() => switchTab("upload")}
          className={`pb-2 text-lg font-medium ${
            activeTab === "upload" ? "border-b-2 border-blue-600 text-blue-600" : "text-gray-600 hover:text-blue-600"
          }`}
        >
          Upload CSV
        </button>
      </div>

      {/* Tab content */}
      <div className="space-y-6">
        {activeTab === "dashboard" && <h2 className="text-2xl font-bold">Welcome to HR Dashboard</h2>}

        {activeTab === "employees" && <EmployeeList />}

        {activeTab === "attendance" && <AttendanceTable />}

        {activeTab === "monthly" && <MonthlyReport />}

        {activeTab === "upload" && <UploadAttendanceCSV />}
      </div>
    </div>
  );
};

export default Dashboard;
