import React, { useEffect, useState } from 'react';
import api from '../../api/api';

const EmployeeDetails = ({ id, onClose }) => {
  const [employee, setEmployee] = useState(null);

  useEffect(() => {
    const fetchEmployee = async () => {
      const { data } = await api.get(`/employees/${id}`);
      setEmployee(data);
    };
    fetchEmployee();
  }, [id]);

  if (!employee) return <p className="text-center py-6">Loading...</p>;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center">
      <div className="bg-white rounded-xl shadow-lg p-6 w-[500px] relative">
        <button
          onClick={onClose}
          className="absolute top-3 right-3 text-gray-600 hover:text-black"
        >
          ✕
        </button>
        <h2 className="text-2xl font-semibold mb-4 text-center">
          {employee.name}
        </h2>

        <div className="space-y-2 text-sm">
          <p><strong>Email:</strong> {employee.email}</p>
          <p><strong>Phone:</strong> {employee.phone || 'N/A'}</p>
          <p><strong>Department:</strong> {employee.department}</p>
          <p><strong>Designation:</strong> {employee.designation}</p>
          <p><strong>Joining Date:</strong> {employee.joining_date?.split('T')[0]}</p>
          {employee.leaving_date && (
            <p><strong>Leaving Date:</strong> {employee.leaving_date?.split('T')[0]}</p>
          )}
          <p><strong>CTC:</strong> ₹{employee.current_ctc || 0}</p>
          <p><strong>Status:</strong> {employee.status}</p>
          <p className="text-gray-500 text-xs mt-2">
            Created: {new Date(employee.createdAt).toLocaleString()}
          </p>
        </div>
      </div>
    </div>
  );
};

export default EmployeeDetails;
