import { createContext, useState, useEffect } from 'react';
import {
  fetchAttendance,
  createAttendance,
  updateAttendance,
  deleteAttendance,
  uploadAttendanceCSV,
} from '../api/attendanceApi';

export const AttendanceContext = createContext();

export const AttendanceProvider = ({ children }) => {
  const [attendance, setAttendance] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // ✅ Fetch attendance records (optionally with filters)
  const getAttendance = async (filters = {}) => {
    setLoading(true);
    setError(null);
    try {
      const data = await fetchAttendance(filters);
      setAttendance(data);
    } catch (err) {
      console.error('Error fetching attendance:', err);
      setError(err.response?.data?.message || 'Failed to load attendance.');
    } finally {
      setLoading(false);
    }
  };

  // ✅ Add a new attendance record
  const addAttendance = async (record) => {
    setError(null);
    try {
      const newRecord = await createAttendance(record);
      setAttendance((prev) => [...prev, newRecord]);
    } catch (err) {
      console.error('Error adding attendance:', err);
      setError(err.response?.data?.message || 'Failed to add record.');
      throw err;
    }
  };

  // ✅ Edit existing attendance record
  const editAttendance = async (id, updated) => {
    setError(null);
    try {
      const updatedRecord = await updateAttendance(id, updated);
      setAttendance((prev) =>
        prev.map((a) => (a._id === id ? updatedRecord : a))
      );
    } catch (err) {
      console.error('Error updating attendance:', err);
      setError(err.response?.data?.message || 'Failed to update record.');
      throw err;
    }
  };

  // ✅ Delete a record
  const removeAttendance = async (id) => {
    setError(null);
    try {
      await deleteAttendance(id);
      setAttendance((prev) => prev.filter((a) => a._id !== id));
    } catch (err) {
      console.error('Error deleting attendance:', err);
      setError(err.response?.data?.message || 'Failed to delete record.');
      throw err;
    }
  };

  // ✅ Bulk upload attendance via CSV
  const uploadCSV = async (file) => {
    setError(null);
    try {
      const result = await uploadAttendanceCSV(file);
      await getAttendance(); // Refresh after upload
      return result;
    } catch (err) {
      console.error('Error uploading CSV:', err);
      setError(err.response?.data?.message || 'Failed to upload CSV.');
      throw err;
    }
  };

  // ✅ Auto-fetch when context mounts
  useEffect(() => {
    getAttendance();
  }, []);

  return (
    <AttendanceContext.Provider
      value={{
        attendance,
        loading,
        error,
        getAttendance,
        addAttendance,
        editAttendance,
        removeAttendance,
        uploadCSV,
      }}
    >
      {children}
    </AttendanceContext.Provider>
  );
};
