import React, { useContext, useEffect } from 'react';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { EmployeeContext } from '../../context/EmployeeContext';

const EmployeeForm = ({ selectedEmployee, onClose }) => {
  const { addEmployee, updateEmployee } = useContext(EmployeeContext);

  const formik = useFormik({
    initialValues: {
      name: '',
      email: '',
      phone: '',
      department: '',
      designation: '',
      joining_date: '',
      leaving_date: '',
      current_ctc: '',
      status: 'Active',
    },
    validationSchema: Yup.object({
      name: Yup.string().required('Name is required'),
      email: Yup.string().email('Invalid email').required('Email is required'),
      department: Yup.string().required('Department is required'),
      designation: Yup.string().required('Designation is required'),
      joining_date: Yup.date().required('Joining date is required'),
    }),
    onSubmit: async (values, { resetForm }) => {
      try {
        if (selectedEmployee) {
          await updateEmployee(selectedEmployee._id, values);
        } else {
          await addEmployee(values);
        }
        resetForm();
        onClose();
      } catch (error) {
        console.error('Error submitting form:', error);
      }
    },
  });

  useEffect(() => {
    if (selectedEmployee) {
      formik.setValues({
        ...selectedEmployee,
        joining_date: selectedEmployee.joining_date?.split('T')[0] || '',
        leaving_date: selectedEmployee.leaving_date?.split('T')[0] || '',
      });
    }
  }, [selectedEmployee, formik]);

  return (
    <div className="max-w-4xl mx-auto bg-white shadow-lg rounded-xl p-6">
      <h2 className="text-2xl font-semibold mb-6">
        {selectedEmployee ? 'Edit Employee' : 'Add Employee'}
      </h2>

      <form onSubmit={formik.handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-x-6 gap-y-6">
        {/* Full Name */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Full Name</label>
          <input
            type="text"
            name="name"
            onChange={formik.handleChange}
            value={formik.values.name}
            className="border rounded-md w-full p-3"
          />
          {formik.touched.name && formik.errors.name && (
            <p className="text-red-500 text-sm mt-1">{formik.errors.name}</p>
          )}
        </div>

        {/* Email */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Email</label>
          <input
            type="email"
            name="email"
            onChange={formik.handleChange}
            value={formik.values.email}
            className="border rounded-md w-full p-3"
          />
          {formik.touched.email && formik.errors.email && (
            <p className="text-red-500 text-sm mt-1">{formik.errors.email}</p>
          )}
        </div>

        {/* Phone */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Phone</label>
          <input
            type="text"
            name="phone"
            onChange={formik.handleChange}
            value={formik.values.phone}
            className="border rounded-md w-full p-3"
          />
        </div>

        {/* Department */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Department</label>
          <input
            type="text"
            name="department"
            onChange={formik.handleChange}
            value={formik.values.department}
            className="border rounded-md w-full p-3"
          />
          {formik.touched.department && formik.errors.department && (
            <p className="text-red-500 text-sm mt-1">{formik.errors.department}</p>
          )}
        </div>

        {/* Designation */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Designation</label>
          <input
            type="text"
            name="designation"
            onChange={formik.handleChange}
            value={formik.values.designation}
            className="border rounded-md w-full p-3"
          />
          {formik.touched.designation && formik.errors.designation && (
            <p className="text-red-500 text-sm mt-1">{formik.errors.designation}</p>
          )}
        </div>

        {/* Joining Date */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Joining Date</label>
          <input
            type="date"
            name="joining_date"
            onChange={formik.handleChange}
            value={formik.values.joining_date}
            className="border rounded-md w-full p-3"
          />
        </div>

        {/* Leaving Date */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Leaving Date</label>
          <input
            type="date"
            name="leaving_date"
            onChange={formik.handleChange}
            value={formik.values.leaving_date}
            className="border rounded-md w-full p-3"
          />
        </div>

        {/* Current CTC */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Current CTC</label>
          <input
            type="number"
            name="current_ctc"
            onChange={formik.handleChange}
            value={formik.values.current_ctc}
            className="border rounded-md w-full p-3"
          />
        </div>

        {/* Status */}
        <div className="flex flex-col">
          <label className="text-sm font-medium mb-2">Status</label>
          <select
            name="status"
            onChange={formik.handleChange}
            value={formik.values.status}
            className="border rounded-md w-full p-3"
          >
            <option value="Active">Active</option>
            <option value="Left">Left</option>
          </select>
        </div>

        {/* Submit Button */}
        <div className="md:col-span-2">
          <button
            type="submit"
            className="bg-blue-600 text-white p-3 rounded-md hover:bg-blue-700 w-full mt-4"
          >
            {selectedEmployee ? 'Update Employee' : 'Add Employee'}
          </button>
        </div>
      </form>
    </div>
  );
};

export default EmployeeForm;
