import { createContext, useState, useEffect } from 'react';
import api from '../api/api';

export const EmployeeContext = createContext();

export const EmployeeProvider = ({ children }) => {
  const [employees, setEmployees] = useState([]);
  const [loading, setLoading] = useState(false);

  // Fetch employees from API
  const fetchEmployees = async (filters = {}) => {
    setLoading(true);
    const { data } = await api.get('/employees', { params: filters });
    setEmployees(data);
    setLoading(false);
  };

  // Add a new employee
  const addEmployee = async (formData) => {
    const { data } = await api.post('/employees', formData);
    setEmployees(prev => [data, ...prev]);
  };

  // Update an existing employee
  const updateEmployee = async (id, formData) => {
    const { data } = await api.put(`/employees/${id}`, formData);
    setEmployees(prev => prev.map(e => (e._id === id ? data : e)));
  };

  // Delete an employee
  const deleteEmployee = async (id) => {
    await api.delete(`/employees/${id}`);
    setEmployees(prev => prev.filter(e => e._id !== id));
  };

  // Fetch employees on mount
  useEffect(() => {
    fetchEmployees();
  }, []);

  return (
    <EmployeeContext.Provider
      value={{
        employees,
        fetchEmployees,
        addEmployee,
        updateEmployee,
        deleteEmployee,
        loading
      }}
    >
      {children}
    </EmployeeContext.Provider>
  );
};
