import React, { useContext, useState } from 'react';
import { EmployeeContext } from '../../context/EmployeeContext';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';

export default function EmployeeList({ onEdit, onView }) {
  const { employees, fetchEmployees, loading, deleteEmployee } = useContext(EmployeeContext);
  const [search, setSearch] = useState('');

  const handleSearch = (e) => {
    e.preventDefault();
    fetchEmployees({ search });
  };

  const exportToExcel = () => {
    if (employees.length === 0) {
      alert('No employee data to export.');
      return;
    }

    const exportData = employees.map(emp => ({
      'Employee ID': emp.employee_id || '',
      'Name': emp.name || '',
      'Email': emp.email || '',
      'Department': emp.department?.name || '',
      'Designation': emp.designation?.name || '',
      'Joining Date': emp.joining_date ? emp.joining_date.split('T')[0] : '',
      'CTC': emp.current_ctc || '',
      'Status': emp.status || '',
    }));

    const ws = XLSX.utils.json_to_sheet(exportData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Employees');
    const buf = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    saveAs(new Blob([buf], { type: 'application/octet-stream' }), 'Employees.xlsx');
  };

  return (
    <div>
      <h2 className="text-2xl font-bold mb-4">Employees</h2>

      {/* Search + Export */}
      <form onSubmit={handleSearch} className="flex gap-2 mb-4">
        <input
          value={search}
          onChange={e => setSearch(e.target.value)}
          placeholder="Search by name / email / department"
          className="border rounded-md px-3 py-2 w-1/3"
        />
        <button type="submit" className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700">Search</button>
        <button type="button" onClick={exportToExcel} className="bg-green-600 text-white px-4 py-2 rounded-md hover:bg-green-700">Export to Excel</button>
      </form>

      {loading ? (
        <p>Loading...</p>
      ) : employees.length === 0 ? (
        <p>No employees found.</p>
      ) : (
        <table className="min-w-full border">
          <thead className="bg-gray-200">
            <tr>
              <th className="px-4 py-2 border">ID</th>
              <th className="px-4 py-2 border">Name</th>
              <th className="px-4 py-2 border">Email</th>
              <th className="px-4 py-2 border">Department</th>
              <th className="px-4 py-2 border">Actions</th>
            </tr>
          </thead>
          <tbody>
            {employees.map(emp => (
              <tr key={emp._id} className="text-center">
                <td className="border px-4 py-2">{emp.employee_id || '-'}</td>
                <td className="border px-4 py-2">{emp.name || '-'}</td>
                <td className="border px-4 py-2">{emp.email || '-'}</td>
                <td className="border px-4 py-2">{emp.department?.name || '-'}</td>
                <td className="border px-4 py-2 space-x-2">
                  <button onClick={() => onView(emp._id)} className="bg-green-500 text-white px-3 py-1 rounded-md hover:bg-green-600">View</button>
                  <button onClick={() => onEdit(emp)} className="bg-yellow-500 text-white px-3 py-1 rounded-md hover:bg-yellow-600">Edit</button>
                  <button onClick={() => deleteEmployee(emp._id)} className="bg-red-600 text-white px-3 py-1 rounded-md hover:bg-red-700">Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}
