import React, { useEffect, useState, useContext } from "react";
import { AttendanceContext } from "../../context/AttendanceContext";

const MonthlyReport = () => {
  const { attendance, getAttendance, loading } = useContext(AttendanceContext);
  const [report, setReport] = useState({}); // { employee_id: { "YYYY-MM-DD": status } }

  useEffect(() => {
    const fetchData = async () => {
      await getAttendance();
    };
    fetchData();
  }, []);

  useEffect(() => {
    const grouped = {};
    attendance.forEach((record) => {
      if (!grouped[record.employee_id]) grouped[record.employee_id] = {};
      grouped[record.employee_id][record.date] = record.status;
    });
    setReport(grouped);
  }, [attendance]);

  const getStatusColor = (status) => {
    switch (status) {
      case "Present":
        return "bg-green-500";
      case "Absent":
        return "bg-red-500";
      case "Leave":
        return "bg-yellow-400";
      default:
        return "bg-gray-200";
    }
  };

  const getMonthDates = () => {
    const now = new Date();
    const year = now.getFullYear();
    const month = now.getMonth();
    const days = new Date(year, month + 1, 0).getDate();
    return Array.from({ length: days }, (_, i) => {
      const date = new Date(year, month, i + 1);
      return date.toISOString().split("T")[0]; // YYYY-MM-DD
    });
  };

  const monthDates = getMonthDates();

  return (
    <div className="p-6 bg-white shadow-md rounded-lg mt-6 overflow-x-auto">
      <h2 className="text-2xl font-semibold mb-4">Monthly Attendance Report</h2>
      {loading ? (
        <p>Loading report...</p>
      ) : (
        <table className="border-collapse border w-full">
          <thead>
            <tr className="bg-gray-100">
              <th className="border p-2 sticky left-0 bg-gray-100">Employee ID</th>
              {monthDates.map((date) => (
                <th key={date} className="border p-2 text-xs">{date.split("-")[2]}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {Object.keys(report).length === 0 ? (
              <tr>
                <td colSpan={monthDates.length + 1} className="text-center p-4 text-gray-500">
                  No attendance data
                </td>
              </tr>
            ) : (
              Object.entries(report).map(([empId, records]) => (
                <tr key={empId}>
                  <td className="border p-2 sticky left-0 bg-white font-medium">{empId}</td>
                  {monthDates.map((date) => (
                    <td
                      key={date}
                      className={`border w-6 h-6 ${getStatusColor(records[date])}`}
                    ></td>
                  ))}
                </tr>
              ))
            )}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default MonthlyReport;
