import React, { useState, useContext } from "react";
import { AttendanceContext } from "../../context/AttendanceContext";

const UploadAttendanceCSV = () => {
  const { uploadCSV } = useContext(AttendanceContext);
  const [file, setFile] = useState(null);
  const [message, setMessage] = useState("");

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
    setMessage("");
  };

  const handleUpload = async () => {
    if (!file) {
      setMessage("Please select a CSV file to upload.");
      return;
    }

    try {
      const result = await uploadCSV(file);
      setMessage(result.message || "CSV uploaded successfully!");
      setFile(null);
    } catch (err) {
      console.error(err);
      setMessage("Failed to upload CSV. Check file format.");
    }
  };

  return (
    <div className="p-6 bg-white shadow-md rounded-lg mt-6">
      <h2 className="text-2xl font-semibold mb-4">Upload Attendance CSV</h2>
      <div className="flex gap-4 items-center">
        <input
          type="file"
          accept=".csv"
          onChange={handleFileChange}
          className="border p-2 rounded"
        />
        <button
          onClick={handleUpload}
          className="bg-purple-600 text-white px-4 py-2 rounded hover:bg-purple-700"
        >
          Upload
        </button>
      </div>
      {message && <p className="mt-2 text-sm text-gray-700">{message}</p>}
    </div>
  );
};

export default UploadAttendanceCSV;
